import React, { useState } from "react";
import Products from "./Products";
import TopMenu from "./TopMenu";
import Fade from "react-reveal/Fade";
import FadingScreen from "./FadingScreen";
// import ObserverWrapper from "./ObserverWrapper";

function Starter() {
  const handleScrollYDir = () => {
    if (!show) {
      if (window.pageYOffset > 840) {
        setShow(true);
      }
    }
  };
  const handleScrollXDir = e => {
    if (e.target.scrollLeft > 100) {
      setDarken("fade-out");
    } else {
      setDarken("fade-in");
    }
    console.log("darken", darken);
    console.log("scroll", e.target.scrollLeft);
  };
  const [show, setShow] = React.useState(false);

  const [lastYPos, setLastYPos] = React.useState(0);
  const [darken, setDarken] = React.useState("fade-in");
  console.log("window.pageYOffset", window.pageYOffset);
  React.useEffect(() => {
    function handleScroll() {
      const yPos = window.scrollY;

      setLastYPos(yPos);
    }

    window.addEventListener("scroll", handleScroll, false);

    return () => {
      window.removeEventListener("scroll", handleScroll, false);
    };
  }, [lastYPos]);

  return (
    <>
      <TopMenu />

      <FadingScreen subject="beauty" yPosition="400" />

      <div onScroll={e => handleScrollXDir(e)} className="insider">
        <div className={darken}>
          <b>
            Discover <br />
            Our <br />
            Beauty Products
          </b>
        </div>
        <Products
          product="product1"
          stars="1"
          description="All kinds of skin"
          name="Skin Cream"
        />
        <Products
          product="product2"
          stars="5"
          description="Classy"
          name="Lipstick"
        />
        <Products
          product="product3"
          stars="4"
          description="Old Ladies"
          name="Eye Cream"
        />
        <Products
          product="product3"
          stars="2"
          description="Young Ladies"
          name="Eye Cream"
        />
        <Products
          product="product4"
          stars="3"
          description="Ever Lasting"
          name="Eyeliner"
        />
        <Products
          product="product4"
          stars="1"
          description="Waterproof"
          name="Eyeliner"
        />
      </div>
      {handleScrollYDir()}
      {show ? <FadingScreen subject="fitness" yPosition="1200" /> : null}
    </>
  );
}

export default Starter;
